body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.App {
  background: url(/weather-reactapp/static/media/weather.5a78d3c5.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  font-size: calc(10px + 2vmin);
  color: rgb(248, 248, 248);
}

.App h1{
  text-transform: uppercase;
  background-color: rgba(17, 16, 16, 0.3);
  padding: .5rem;
}

.content {
  background-color: rgba(17, 16, 16, 0.3);
  padding: 1rem;
  margin: 1rem;
}

.App input{
  outline: none;
  font-size: 1.2rem;
  background: none;
  border: none;
  border-bottom: 1.5px solid rgba(17, 16, 16, 0.3);
}
::-webkit-input-placeholder{
  color:rgba(255, 255, 255, 0.3);
  text-align: center;
 
}
::-ms-input-placeholder{
  color:rgba(255, 255, 255, 0.3);
  text-align: center;
 
}
::placeholder{
  color:rgba(255, 255, 255, 0.3);
  text-align: center;
 
}

.App button{
  width: 7rem;
  height: 2rem;
  border: none;
  background: rgba(160, 40, 40, 0.8);
  color: white;
  outline: none;
  margin: 1rem;
}

form{
  display: flex;
  flex-direction: column;
  align-items: center;
}
